import React, { Component } from 'react';
import { Forecast } from '../api/WeatherResponse';

export default class WeatherRow extends Component<{forecast: Forecast | null}, {}> {
    render() {
        console.log(this.props.forecast);
        if (this.props.forecast !== null) {
            return <div>
                <div className="weather-header">{this.props.forecast.TimePeriodName}</div>
                <img className="weather-img" src={this.props.forecast.IconUrl} />
                <div className="weather-temp">{this.props.forecast.Temperature + "°F"}</div>
                <div className="weather-detailed">{this.props.forecast.DetailedForecast}</div>
            </div>;
        }
        return <div className="weather-header">Missing Weather Data</div>;
    }
}