import WeatherResponse, { Forecast } from './WeatherResponse';
import FetchProxy from './FetchProxy';

export default class WeatherApi {
    public static getWeather(lotXValue: number, lotYValue: number) : Promise<WeatherResponse> {
        return new Promise(resolve => {
            let idsString: string = lotXValue + ',' + lotYValue;
            let url: string = `https://api.weather.gov/gridpoints/LOT/${idsString}/forecast`;
            FetchProxy.fetchViaProxy(url)
            .then(response => response.json())
            .then(body => {
                console.log(body);
                let response = new Array<Forecast>();
                body['properties']['periods'].forEach((period: { [x: string]: string; }) => {
                    let forecast = new Forecast(period['name'], Number.parseFloat(period['temperature']),
                    period['shortForecast'], period['detailedForecast'], period['icon'])
                    response.push(forecast);
                });
                resolve(new WeatherResponse(response));
            });
        });
    }
}