import React, { Component } from 'react';
import WeatherApi from '../api/WeatherApi';
import WeatherResponse, { Forecast } from '../api/WeatherResponse';
import WeatherRow from './WeatherRow';

export default class Weather extends Component<{dataIndex: number}, {isFetching: boolean, forecasts: Array<Forecast>}> {
    timer: number;
    constructor(props: any) {
        super(props);
        this.timer = 0;
        this.state = {
            isFetching: false,
            forecasts: new Array<Forecast>()
        }
    }

    render() {
        return <WeatherRow forecast={this.state.forecasts.length > this.props.dataIndex ? this.state.forecasts[this.props.dataIndex] : null} />
    }

    componentDidMount() {
        this.fetchData();
        this.timer = setInterval(() => this.fetchData(), 30 * 1000);
    }

    async fetchData() {
        this.setState({
            isFetching: true
        })
        WeatherApi.getWeather(73, 70).then((response: WeatherResponse) => {
            this.setState({forecasts: response.Forecasts});
        });
        this.setState({
            isFetching: false
        })
    }
}